import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import dataSource from './views/nav1/dataSource.vue'
import metaData from './views/nav1/metaData.vue'
import batch from './views/task/batch.vue' // 批量执行
import schedule from './views/task/schedule.vue' // 定时任务
import log from './views/log/index.vue'
import result from './views/log/result.vue'
import product from './views/product/index.vue' // 产品
import echarts from './views/charts/echarts.vue'
import nav from './views/charts/index.vue'
import page7 from './views/metaDatasource/datasource.vue'

let routes = [
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  // { path: '/main', component: Main },
  {
    path: '/',
    component: Home,
    name: '元数据管理',
    iconCls: 'fa fa-adjust', // 图标样式class
    children: [
      { path: '/main', component: Main, name: '主页', hidden: true },
      { path: '/dataSource', component: dataSource, name: '数据源' },
      // { path: '/ceshi', component: ceshi, name: '测试' },

    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    leaf: true, // 只有一个节点
    iconCls: 'fa fa-product-hunt',
    children: [
      { path: '/product', component: product, name: '产品'}
    ]
  },
  {
    path: '/',
    component: Home,
    name: '测试',
    iconCls: 'fa fa-th-list',
    children: [
      { path: '/batch', component: batch, name: '测试任务' },
      { path: '/schedule', component: schedule, name: '批量执行' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '测试日志',
    leaf: true, // 只有一个节点
    iconCls: 'fa fa-file',
    children: [
      { path: '/log', component: log, name: '日志' },
    // { path: '/result', component:result, name: '结果' },
    ]
  },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-address-card',
  //     leaf: true,//只有一个节点
  //     children: [
  //         { path: '/log', component: log, name: '日志' },
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-address-card',
  //     leaf: true,//只有一个节点
  //     children: [
  //         { path: '/log', component: log, name: '日志' },
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: 'Charts',
  //     iconCls: 'fa fa-bar-chart',
  //     children: [
  //         { path: '/nav', component: nav, name: '目录' }
  //     ]
  // },
  {
    path: '*',
    hidden: true,
    redirect: { path: '/404' }
  }
]
export default routes



