<template>
 	 <section>
  	 	<el-button type="primary" plain>添加菜单</el-button>
  	 	<el-table
		    ref="multipleTable"
		    :data="tableData"
		    tooltip-effect="dark"
		    border
		    style="width: 100%"
		    @selection-change="handleSelectionChange">
		    <el-table-column
			    type="selection"
			      width="55">
			</el-table-column>
		    <el-table-column
		      fixed
		      prop="date"
		      label="日期"
		      width="150">
		    </el-table-column>
		    <el-table-column
		      prop="name"
		      label="姓名"
		      width="120">
		    </el-table-column>
		    <el-table-column
		      prop="province"
		      label="省份"
		      width="120">
		    </el-table-column>
		    <el-table-column
		      prop="city"
		      label="市区"
		      width="120">
		    </el-table-column>
		    <el-table-column
		      prop="address"
		      label="地址"
		      width="300">
		    </el-table-column>
		    <el-table-column
		      prop="zip"
		      label="邮编"
		      width="120">
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label="操作"
		      width="100">
		      <template slot-scope="scope">
		        <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
		        <el-button type="text" size="small">编辑</el-button>
		      </template>
		    </el-table-column>
		  </el-table>
  	</section>
</template>
<script type="text/javascript">
	import echarts from 'echarts'

    export default {
        data() {
            return {
                tableData:[
                	{},
                	{},
                ]
            }
        },

        methods: {
        }
    }

</script>
<style lang="scss" type="text/css">
	.el-button {
		margin-top: 20px;
	}
</style>


