<template>
	<div>
	  <p class="fanhui"><span @click="product()">子产品1</span> / <span style="color: #97a8be">测试集</span></p>
	  <el-button type="primary" plain @click="product">返回</el-button>
	  <el-button type="primary" plain @click="addProduct">添加测试集</el-button>
		<el-form id="addProduct" :class="{active:isProduct}" ref="form" :model="form" label-width="80px">
		  <el-form-item label="名称">
		    <el-input v-model="form.name"></el-input>
		  </el-form-item>
		  <el-form-item label="案例数量">
		    <el-input v-model="form.number" :disabled="true"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSubmit()">立即创建</el-button>
		    <el-button @click="cancel()">好了，不创建了</el-button>
		  </el-form-item>
		</el-form>
		<el-form id="addBianji" :class="{active:isBianji}" ref="form2" :model="form2" label-width="80px">
		  <el-form-item label="名称">
		    <el-input v-model="form2.name"></el-input>
		  </el-form-item>
		  <el-form-item label="案例数量">
		    <el-input v-model="form2.number" :disabled="true"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSubmit2(scope.$index, scope.row)">修改</el-button>
		    <el-button @click="cancel()">取消</el-button>
		  </el-form-item>
		</el-form>
  	 	<el-table
		    ref="multipleTable"
		    :data="tableData"
		    tooltip-effect="dark"
		    border
		    style="width: 100%;margin-top:10px;"
		    @selection-change="handleSelectionChange">
		    <el-table-column
			    type="selection"
			    width="55">
			</el-table-column>
			    <!-- fixed -->
		    <el-table-column
		      prop="name"
		      label="名称"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="father"
		      label="案例数量"
		      >
		    </el-table-column>
		      <!-- fixed="right" -->
		    <el-table-column
		      label="操作"
		      width="220">
		      <template slot-scope="scope">
		        <el-button @click="handleClick(scope.row)" type="text" size="small">查看案例</el-button>
		        <el-button type="text" size="small" @click="handleSee(scope.row)">编辑</el-button>
		        <el-button
		          size="mini"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		  <task id="task" :class="{active:isTask}"  />
	</div>
</template>
<script type="text/javascript">
	import task from './task'
	export default {
	  	name: '',
	  	props: {
		  	isTest:false,
		},
		components: {
			task,
		},
		data() {
            return {
            	isTask:false,
            	isBianji:false,
            	isProduct:false,
                tableData:[
                	{name:'1',number:'父产品'},
                	{name:'1',number:'父产品'},
                	{name:'1',number:'父产品'},
                ],
                form2:{
                	name:'',
                	number:'父产品名称'
                },
                form:{
                		name:'',
                		number:''
                	}
                
            }
        },
        methods: {
        	handleClick(val){
        		console.log(this.isTask)
				this.isTask = true;
        	},
        	handleDelete(_index,obj){
        		this.tableData.splice(_index,1)
        	},
        	handleSee(val){
        		this.form2.name = val.name
        		// 编辑
        		this.isBianji = true
        	},
        	handleSelectionChange(val) {
        		this.multipleSelection = val;
      		},
      		addProduct(){
      			this.isProduct = true;
      		},
      		onSubmit() {
		        // 提交子产品
		        this.tableData.push({
		        	name:this.form.name,
		        	number:this.form.number
		        })
                // this.$refs["form"].resetFields();

		    },
		    onSubmit2(){

		    },
		    cancel() {
				this.isProduct = false;
				this.isBianji = false;
		    },
		    // 判断是否展示
		    product() {
				this.$emit('listenToChild',false)
		    }
		    
        },
}
</script>
<style lang="scss" type="text/css">
@import '~scss_vars';
	.fanhui {
		cursor: pointer;
	}
	#task {
		/*display: none;*/
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		right: -1900px;
		background: $color-major;
		z-index: 20;
		transition: right 1s;
		-moz-transition: right 1s; /* Firefox 4 */
		-webkit-transition: right 1s; /* Safari 和 Chrome */
		-o-transition: right 1s; /* Opera */
		&.active {
			right: 0;
		}
	}
</style>


