<template>
	<div>
	  <p class="fanhui">
	  	<span @click="">子产品</span> / 
	  	<span>测试集</span> / 
	  	<span style="color: #97a8be">案例列表</span>
	  </p>
	  <el-button type="primary" plain @click="product">返回</el-button>
	  <el-button type="primary" plain @click="addProduct">添加案例</el-button>
	  <el-button type="primary" plain @click="addProduct">执行</el-button>
	  <!-- 增加 -->
		<el-form id="addProduct" :class="{active:isProduct}" ref="form" :model="form" label-width="100px">
		  <el-form-item label="名称">
		    <el-input v-model="form.name"></el-input>
		  </el-form-item>
		  <el-form-item label="最大结果数量">
		    <el-input v-model="form.number"></el-input>
		  </el-form-item>
		  <el-form-item label="是否SQL案例">
		    <el-input v-model="form.SQLBlooe"></el-input>
		  </el-form-item>
		  <el-form-item label="数据源">
		    <el-input v-model="form.dataSource"></el-input>
		  </el-form-item>
		  <el-form-item label="SQL语句">
		    <el-input v-model="form.sql"></el-input>
		  </el-form-item>
		  <el-form-item label="KEY">
		    <el-input v-model="form.key"></el-input>
		  </el-form-item>
		  <el-form-item label="字段">
		    <el-input v-model="form.test"></el-input>
		  </el-form-item>
		  <el-form-item label="规则" v-for="(list, index) in form.rules" :key="list.key">
		    <el-input v-model="list.source" style="width: 28%;"></el-input>
		    <el-input v-model="list.rule" style="width: 28%;"></el-input>
		    <el-input v-model="list.target" style="width: 28%;"></el-input>
			    <el-button style="width: 10%" size="mini" type="danger" @click.prevent="rulesDelete(list)">删除</el-button>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSubmit()">立即创建</el-button>
		    <el-button type="primary" @click="addRules()">添加规则</el-button>
		    <el-button @click="cancel()">好了，不创建了</el-button>
		  </el-form-item>
		</el-form>
		<!-- 编辑 -->
		<el-form id="addBianji" :class="{active:isBianji}" ref="form2" :model="form2" label-width="100px">
		  <el-form-item label="名称">
		    <el-input v-model="form2.name"></el-input>
		  </el-form-item>
		  <el-form-item label="最大结果数量">
		    <el-input v-model="form2.number"></el-input>
		  </el-form-item>
		  <el-form-item label="是否SQL案例">
		    <el-input v-model="form2.SQLBlooe"></el-input>
		  </el-form-item>
		  <el-form-item label="数据源">
		    <el-input v-model="form2.dataSource"></el-input>
		  </el-form-item>
		  <el-form-item label="SQL语句">
		    <el-input v-model="form2.sql"></el-input>
		  </el-form-item>
		  <el-form-item label="KEY">
		    <el-input v-model="form2.key"></el-input>
		  </el-form-item>
		  <el-form-item label="字段">
		    <el-input v-model="form2.test"></el-input>
		  </el-form-item>
		  <el-form-item label="规则" v-for="(list, index) in form2.rules" :key="list.key">
		    <el-input v-model="list.source" style="width: 28%;"></el-input>
		    <el-input v-model="list.rule" style="width: 28%;"></el-input>
		    <el-input v-model="list.target" style="width: 28%;"></el-input>
			    <el-button style="width: 10%" size="mini" type="danger" @click.prevent="rulesDelete(list)">删除</el-button>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSubmit2(scope.$index, scope.row)">修改</el-button>
		    <el-button @click="cancel()">取消</el-button>
		  </el-form-item>
		</el-form>
  	 	<el-table
		    ref="multipleTable"
		    :data="tableData"
		    tooltip-effect="dark"
		    border
		    style="width: 100%;margin-top:10px;"
		    @selection-change="handleSelectionChange">
		    <el-table-column
			    type="selection"
			    width="55">
			</el-table-column>
			    <!-- fixed -->
		    <el-table-column
		      prop="name"
		      label="名称"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="number"
		      label="最大结果数量"
		      >
		      </el-table-column>
		      <el-table-column
		      prop="SQL"
		      label="是否SQL案例"
		      >
		    </el-table-column>
		    <el-table-column
		      label="操作"
		      width="220">
		      <template slot-scope="scope">
		        <el-button @click="handleClick(scope.row)" type="text" size="small">查看详情</el-button>
		        <el-button type="text" size="small" @click="handleSee(scope.row)">编辑</el-button>
		        <el-button
		          size="mini"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		      </template>
		    </el-table-column>
		  </el-table>
	</div>
</template>
<script type="text/javascript">
	export default {
	  name: '',
	  props: {
		  // isTest:false,
		},
	data() {
            return {
            	isBianji:false,
            	isProduct:false,
                tableData:[
                	{name:'name',number:'1',SQLBlooe:'是',dataSource:'dataSource',sql:'sql语句',key:'c1',test:'c1,c2,c3,c4...',rules:[{source:'字段1',rule:'规则1',target:'目标字段'}]},
                	{name:'name',number:'1',SQLBlooe:'是',dataSource:'dataSource',sql:'sql语句',key:'c1',test:'c1,c2,c3,c4...',rules:[{source:'字段1',rule:'规则1',target:'目标字段'}]},
                	{name:'name',number:'1',SQLBlooe:'是',dataSource:'dataSource',sql:'sql语句',key:'c1',test:'c1,c2,c3,c4...',rules:[{source:'字段1',rule:'规则1',target:'目标字段'}]},
                ],
                form2:{
                	name:'',
            		number:'',
            		SQLBlooe:'',
            		dataSource:'',
            		sql:'',
            		key:'',
            		test:'',
            		rules:[{source:'',rule:'',target:''}]
                },
                form:{
            		name:'',
            		number:'',
            		SQLBlooe:'',
            		dataSource:'',
            		sql:'',
            		key:'',
            		test:'',
            		rules:[{source:'',rule:'',target:''}]
            	}
                
            }
        },
        methods: {
        	handleClick(val){
				
        	},
        	handleDelete(_index,obj){
        		this.tableData.splice(_index,1)
        	},
        	handleSee(val){
        		this.form2.name = val.name
        		this.form2.number = val.number
        		this.form2.SQLBlooe = val.SQLBlooe
        		this.form2.dataSource = val.dataSource
        		this.form2.sql = val.sql
        		this.form2.key = val.key
        		this.form2.test = val.test
        		this.form2.rules = val.rules
        		// 编辑
        		this.isBianji = true
        	},
        	handleSelectionChange(val) {
        		this.multipleSelection = val;
      		},
      		addProduct(){
      			this.isProduct = true;
      		},
      		onSubmit() {
		        // 提交子产品
		        this.tableData.push({
		        	name:this.form.name,
		        	number:this.form.number,
		        	SQLBlooe:this.form.SQLBlooe,
		        	dataSource:this.form.dataSource,
		        	sql:this.form.sql,
		        	key:this.form.key,
		        	test:this.form.test,
		        	rules:this.form.rules,
		        })
                // this.$refs["form"].resetFields();

		    },
		    onSubmit2(){

		    },
		    cancel() {
				this.isProduct = false;
				this.isBianji = false;
		    },
		    // 判断是否展示
		    product() {
				this.$emit('listenToChild',false)
		    },
		    //  增加案例——添加规则
		    addRules(){
		    	this.form.rules.push({source:'',rule:'',target:''})
		    },
		    // 删除规则
		    rulesDelete(item){
		        var index = this.form.rules.indexOf(item)
		        if (index !== -1) {
		          this.form.rules.splice(index, 1)
		        }
        	},
		    
        },
}
</script>
<style lang="scss" scoped type="text/css">
	.fanhui {
		cursor: pointer;
		a {
			text-decoration: none;
			color: #000000;
		}
	}
	#addProduct,#addBianji {
		left: 5%;
		width: 80%;
		top: 0;
		height: 380px;
		overflow-y: scroll;
	}
</style>


